import React from "react"
import Link from "../overrides/Link"
import {
  Box,
  Stack,
  Icon,
  Popover,
  Text,
  PopoverTrigger,
  PopoverContent,
} from '@chakra-ui/react';
import {
  ChevronDownIcon
} from '@chakra-ui/icons';
import DesktopSubNav from './DesktopSubNav'

import { useStaticQuery, graphql } from "gatsby"
import { flatListToHierarchical } from "../../utils"

import { useLocation } from "@reach/router"

const DesktopNav = (  ) => {
  const data = useStaticQuery(graphql`
    {
      allWpMenuItem(
        sort: { fields: order, order: ASC }
        filter: { menu: { node: { name: { eq: "primary" } } } }
      ) {
        nodes {
          id
          title: label
          path
          target
          parent: parentId
        }
      }
    }
  `)

  const location = useLocation()

  const NAV_ITEMS = flatListToHierarchical(data.allWpMenuItem.nodes, {
    idKey: "id",
    childrenKey: "routes",
    parentKey: "parent",
  })
  
  return (
    <Stack display={["none", "none", "none", "flex"]} direction={'row'} ml={12} spacing={4}>
      {NAV_ITEMS.map((navItem) => (
        <Box key={navItem.id}>
          <Popover trigger={'hover'} placement={'bottom-start'}>
            <PopoverTrigger>
              <Box
              borderBottom={'2px solid'}
              borderColor={location.pathname === navItem.path  ? 'orange.400' : '#ffffff00'}
              mx={2}
              _hover={{
                borderBottom: "2px solid",
                borderColor:'orange.400'
              }}
              >
              <Link to={navItem.path ?? '#'}>
                <Text
                fontWeight={location.pathname === navItem.path  ? '700' : '500'}
                color='white'
                >
                {navItem.title}{navItem.routes.length > 0 && (<Icon as={ChevronDownIcon} />)}
                </Text>
                
              </Link>
              </Box>
            </PopoverTrigger>

            {navItem.routes.length > 0 && (
              <PopoverContent
                border={0}
                boxShadow={'xl'}
                bg='#fafafa'
                py={4}
                rounded={'md'}
                minW={'xs'}>
                <Stack >
                  {navItem.routes.map((child) => (
                    <DesktopSubNav key={child.id} {...child} />
                  ))}
                </Stack>
              </PopoverContent>
            )}
          </Popover>
        </Box>
      ))}
    </Stack>
  );
};

  export default DesktopNav;