import React from "react"
import { useStaticQuery } from 'gatsby'
import {
    Box,
    Stack,
    Text,
    Image
  } from '@chakra-ui/react';
  import { FaEnvelope, FaPhone } from 'react-icons/fa';
  import parse from 'html-react-parser'


  const FooterWidget = () => {
      const widgetData = useStaticQuery(graphql`
      {
        allWpSiteWidget(sort: {fields: title}) {
          nodes {
            id
            title
            content
            widgetOptions {
              emailAddress
              phoneNumber
            }
          }
        }
      }
    `)

    const widgets = widgetData.allWpSiteWidget.nodes
    

    return (
        <Stack spacing={6}>
              <Box>
                <Image maxWidth="70px" src="https://www.dovertwg.com/wp-content/uploads/logo-white-retina-1.png" alt="TWG Logo" />
              </Box>
              {widgets.map((widget) => {
                if (widget.title === 'footer-widget-1'){
                  return (
                    <Box key={widget.id}>
                    {parse(widget.content)}
                    </Box>
                  )
                }
                    
                  
                if (widget.title === 'footer-widget-2'){
                      return (
                      <Box key={widget.id}>
                        {parse(widget.content)}
                          <Stack pt={4} direction="row" alignItems="center">
                          <FaPhone fontSize={16} />
                          <Text pl={2} as="a" href={'tel:'+widget.widgetOptions.phoneNumber}>{widget.widgetOptions.phoneNumber}</Text>
                          </Stack>
                          <Stack direction="row" alignItems="center">
                          <FaEnvelope fontSize={16} />
                          <Text pl={2} as="a" href={'mailto:'+widget.widgetOptions.emailAddress}>{widget.widgetOptions.emailAddress}</Text>
                          </Stack>
                        </Box>
                      )
                }
              }
              )}
        </Stack>
    )
  }


  export default FooterWidget