import React from "react"
import Link from "../overrides/Link"
import {
    Flex,
    Box,
    Stack,
    Text,
    useDisclosure,
} from '@chakra-ui/react';
import {
    ChevronDownIcon
} from '@chakra-ui/icons';
import { useLocation } from "@reach/router"


const MobileNavItem = ( item ) => {
  const { isOpen, onToggle } = useDisclosure();
  const href = item.path
  const label = item.title
  const location = useLocation()
  return (
    <Stack mt="2px !important" key={item.id} spacing={4} onClick={item.routes.length>0 ? onToggle : undefined}>
      <Flex
        justify={'space-between'}
        align={'center'}
        _hover={{
          textDecoration: 'none',
        }}>
          <Box>
          {href === '#' ? (
            <Text >
              {label}
            </Text>
          ) : (
            <Link to={href ?? '#'}>
              {label}
            </Link>
          )}   
          
          </Box>
      </Flex>
        <Stack
          ml={2}
          pl={4}
          mt='2px !important'
          align={'start'}>
          {item.routes.length > 0 &&
            item.routes.map((child) => (
              <Box mt="2px !important" key={child.id + child.path}>
              <Link  key={child.id} href={child.path}>
              – {child.title}
              </Link>
              </Box>
            ))}
        </Stack>
    </Stack>
  );
};

export default MobileNavItem