import React from "react"
import {
    Stack,
} from '@chakra-ui/react'
import FooterNavItem from './FooterNavItem'
import { useStaticQuery, graphql } from "gatsby"
import { flatListToHierarchical } from "../../utils"


const FooterNav = () => {
  const data = useStaticQuery(graphql`
    {
      allWpMenuItem(
        sort: { fields: order, order: ASC }
        filter: { menu: { node: { name: { eq: "footer" } } } }
      ) {
        nodes {
          id
          title: label
          path
          target
          parent: parentId
        }
      }
    }
  `)

const displayMobile = ["block", "block", "block", "none"]
const displaySecondary = ["none", "none", "none", "block"]


  const NAV_ITEMS = flatListToHierarchical(data.allWpMenuItem.nodes, {
    idKey: "id",
    childrenKey: "routes",
    parentKey: "parent",
  })

  

  return (
    <Stack py={1}>
      {NAV_ITEMS.map((navItem) => (
        <FooterNavItem key={navItem.id} {...navItem} />
      ))}
    </Stack>
  );
};

//<MobileNavItem key={navItem.label} {...navItem} />

  export default FooterNav

  