import React from "react"
import {
    chakra,
    Button,
    Drawer,
    DrawerBody,
    DrawerOverlay,
    DrawerContent,
    useDisclosure,
  } from "@chakra-ui/react"
  import {
    FaBars,
    FaTimes,
  } from "react-icons/fa"
import MobileNav from './MobileNav'

import { useStaticQuery, graphql } from "gatsby"

const SecondaryNav = () => {
    const { isOpen, onOpen, onClose } = useDisclosure()
    const btnRef = React.useRef()

    const menuData = useStaticQuery(graphql`
    {
      allWpMenuItem(
        sort: { fields: order, order: ASC }
        filter: { menu: { node: { name: { eq: "Secondary" } } } }
      ) {
        nodes {
          id
          title: label
          path
          target
          parent: parentId
        }
      }
    }
  `)
  
    return (
      <>
        <Button
          ref={btnRef}
          colorScheme="gray"
          variant="outline"
          border="0"
          _hover={{
            bgColor: '#ffffff00'
            
          }}
          onClick={isOpen ? onClose : onOpen}
          display={["none", "none", "none", "block"]}
          aria-label="Open Nav"
        >
          {isOpen ? <FaTimes /> : <FaBars color="white" />}
          <chakra.span sx={{ display: "none" }}>Open Secondary Navigation</chakra.span>
        </Button>
        <Drawer
          isOpen={isOpen}
          placement="right"
          onClose={onClose}
          finalFocusRef={btnRef}
        >
          <DrawerOverlay>
            <DrawerContent>
  
              <DrawerBody
              pt={20}
              >
                <MobileNav menuName="secondary" menuData={menuData} displayLocation="secondary" display="flex" />
              </DrawerBody>
  
            
            </DrawerContent>
          </DrawerOverlay>
        </Drawer>
      </>
    )
  }

  export default SecondaryNav