import React, { useState } from 'react';
import { withPrefix, useStaticQuery, graphql } from 'gatsby';
import { SEOContext } from 'gatsby-plugin-wpgraphql-seo';
import {
  chakra,
  ChakraProvider,
  Box,
  Button,
  Flex,
  Drawer,
  DrawerBody,
  DrawerOverlay,
  DrawerContent,
  Spacer,
  useDisclosure,
} from "@chakra-ui/react"
import {
  FaBars,
  FaTimes,
} from "react-icons/fa"
import Link from "./overrides/Link"
import Logo from "./Logo"
import Container from "./Container"
import DesktopNav from "./nav/DesktopNav"
import MobileNav from "./nav/MobileNav"
import SecondaryNav from './nav/SecondaryNav'
import { extendTheme } from "@chakra-ui/react"
import Footer from "./Footer"

const theme = extendTheme({
  fonts: {
    heading: `Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"`,
    body: `Lato, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"`,
    mono: `SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace`,
  }
})

const DrawerNavMobile = () => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  const btnRef = React.useRef()
  

  return (
    <>
      <Button
        ref={btnRef}
        colorScheme="gray"
        variant="outline"
        border="0"
        _hover={{
          bgColor: '#ffffff00'  
        }}
        onClick={isOpen ? onClose : onOpen}
        display={["block", "block", "block", "none"]}
        aria-label="Open Nav"
      >
        {isOpen ? <FaTimes /> : <FaBars color="white"/>}
        <chakra.span sx={{ display: "none" }}>Open mobile nav</chakra.span>
      </Button>
      <Drawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
        finalFocusRef={btnRef}
      >
        <DrawerOverlay>
          <DrawerContent>

            <DrawerBody>
              <MobileNav menuName="primary" displayLocation="mobile" display="flex" />
            </DrawerBody>

          
          </DrawerContent>
        </DrawerOverlay>
      </Drawer>
    </>
  )
}

const Layout = (props) => {
  const bg = "white"
  const btnRef = React.useRef()
  const {
    wp: { seo },
  } = useStaticQuery(graphql`
    query SiteInfoQuery {
        wp {
            seo {
                contentTypes {
                    post {
                        title
                        schemaType
                        metaRobotsNoindex
                        metaDesc
                    }
                    page {
                        metaDesc
                        metaRobotsNoindex
                        schemaType
                        title
                    }
                }
                webmaster {
                    googleVerify
                    yandexVerify
                    msVerify
                    baiduVerify
                }
                schema {
                    companyName
                    personName
                    companyOrPerson
                    wordpressSiteName
                    siteUrl
                    siteName
                    inLanguage
                    logo {
                        sourceUrl
                        mediaItemUrl
                        altText
                    }
                }
                social {
                    facebook {
                        url
                        defaultImage {
                            sourceUrl
                            mediaItemUrl
                        }
                    }
                    instagram {
                        url
                    }
                    linkedIn {
                        url
                    }
                    mySpace {
                        url
                    }
                    pinterest {
                        url
                        metaTag
                    }
                    twitter {
                        username
                    }
                    wikipedia {
                        url
                    }
                    youTube {
                        url
                    }
                }
            }
        }
    }
`)

  return (
    <SEOContext.Provider value={{ global: seo }}>
      <ChakraProvider theme={theme}>
      <Box>
        <chakra.header
          top="0"
          position="absolute"
          zIndex="99"
          bg='#ffffff00'
          left="0"
          right="0"
          width="full"
          {...props}
        >
          <Container pt={0} px={0} mt="0" height="auto" mx="auto" minHeight="0">
            <Flex
              w="100%"
              h="100%"
              py={3}
              px={5}
              align="center"
              justify="space-between"
            >
              <Flex align="center">
                <Link to="/" aria-label="Home Page">
                  <Logo />
                  <chakra.span sx={{ display: "none" }}>Homepage</chakra.span>
                </Link>
                <DesktopNav display={{ base: "none", md: "flex" }} />
              </Flex>

              <Flex>
                <Spacer mx={1} />
                <SecondaryNav btnRef={btnRef} />
                <Spacer mx={1} />
                <DrawerNavMobile btnRef={btnRef} />
              </Flex>
            </Flex>
          </Container>
        </chakra.header>
        <Box as="main">
          {props.children}
        </Box>
        
              <Footer />
           
      </Box>
      </ChakraProvider>
    </SEOContext.Provider>
  )
}

export default Layout