import React from "react"
import {
  chakra,
  Stack,
  VisuallyHidden,
} from '@chakra-ui/react'
import { useStaticQuery, graphql } from "gatsby"
import { flatListToHierarchical } from "../../utils"
import { FaEnvelope, FaFacebook, FaInstagram, FaLinkedin, FaPhone, FaTwitter, FaYoutube } from 'react-icons/fa';

const SocialButton = ({
  children,
  label,
  href,
}) => {
  return (
    <chakra.button
      bg='transparent'
      w={12}
      h={12}
      cursor={'pointer'}
      as={'a'}
      href={href}
      display={'inline-flex'}
      alignItems={'center'}
      justifyContent={'flex-start'}
      size="lg"
      >
      <VisuallyHidden>{label}</VisuallyHidden>
      {children}
    </chakra.button>
  )
}

const FooterNav = () => {
  const data = useStaticQuery(graphql`
    {
      allWpMenuItem(
        sort: { fields: order, order: ASC }
        filter: { menu: { node: { name: { eq: "social" } } } }
      ) {
        nodes {
          id
          title: label
          path
          target
          parent: parentId
        }
      }
    }
  `)

const displayMobile = ["block", "block", "block", "none"]
const displaySecondary = ["none", "none", "none", "block"]


  const NAV_ITEMS = flatListToHierarchical(data.allWpMenuItem.nodes, {
    idKey: "id",
    childrenKey: "routes",
    parentKey: "parent",
  })

  

  return (

    <Stack direction={'row'} spacing={2}>
       {NAV_ITEMS.map((navItem) => {
         switch (navItem.title) {
          case 'Linkedin':
            return (
              <SocialButton key={navItem.id} {...navItem} label={navItem.title} href={navItem.path}>
                <FaLinkedin fontSize={28}/>
              </SocialButton>
            )

          case 'Facebook':
            return (
              <SocialButton key={navItem.id} {...navItem} label={navItem.title} href={navItem.path}>
                <FaFacebook fontSize={28}/>
              </SocialButton>
            )
          
          case 'Twitter':
            return (
              <SocialButton key={navItem.id} {...navItem} label={navItem.title} href={navItem.path}>
                <FaTwitter fontSize={28}/>
              </SocialButton>
            )

            case 'Youtube':
              return (
                <SocialButton key={navItem.id} {...navItem} label={navItem.title} href={navItem.path}>
                  <FaYoutube fontSize={28}/>
                </SocialButton>
              )
  
            case 'Instagram':
              return (
                <SocialButton key={navItem.id} {...navItem} label={navItem.title} href={navItem.path}>
                  <FaInstagram fontSize={28}/>
                </SocialButton>
              )
            
  
            default:
            return 
        }
       }
      )}
    </Stack>
    
     
    
  );
};

//<MobileNavItem key={navItem.label} {...navItem} />

  export default FooterNav

  