import React from "react"
import Link from "../overrides/Link"
import {
    Flex,
    Box,
    Stack,
    Collapse,
    Icon,
    useDisclosure,
} from '@chakra-ui/react';
import {
    ChevronDownIcon
} from '@chakra-ui/icons';
import { useLocation } from "@reach/router"


const MobileNavItem = ( item ) => {
  const { isOpen, onToggle } = useDisclosure()
  const href = item.path
  const label = item.title
  const location = useLocation()
  return (
    <Stack key={item.id} spacing={4} onClick={item.routes.length>0 ? onToggle : undefined}>
      <Flex
        py={2}
        justify={'space-between'}
        align={'center'}
        _hover={{
          textDecoration: 'none',
        }}>
          <Box
              borderBottom={location.pathname === item.path  ? '2px solid' : '0'}
              borderColor={'orange.400'}
              mx={2}
              >
                
          <Link fontWeight={900} to={href ?? '#'}>
            {label}
          </Link>
          </Box>
       
        {item.routes.length > 0 && (
          <Icon
            as={ChevronDownIcon}
            transition={'all .25s ease-in-out'}
            transform={isOpen ? 'rotate(180deg)' : ''}
            w={6}
            h={6}
          />
        )}
      </Flex>

      <Collapse in={isOpen} animateOpacity mt='0' style={{ marginTop: '0!important' }}>
        <Stack
          my={[2,2,2,3]}
          ml={2}
          pl={4}
          borderLeft={1}
          borderStyle={'solid'}
          borderColor={"orange.300"}
          align={'start'}>
          {item.routes.length > 0 &&
            item.routes.map((child) => (
              <Box key={child.id + child.path}
              borderBottom={location.pathname === child.path  ? '2px solid' : '0'}
              borderColor={'orange.400'}
              >
              <Link key={child.id} py={2} href={child.path}>
                {child.title}
              </Link>
              </Box>
            ))}
        </Stack>
      </Collapse>
    </Stack>
  );
};

export default MobileNavItem