import React from "react"
import Link from "../overrides/Link"
import {
    Box,
    Text,
    Stack,
} from '@chakra-ui/react';
import { useLocation } from "@reach/router"

const DesktopSubNav = ( child ) => {
  const href = child.path
  const label = child.title
  const location = useLocation()
  return (
    <Link
      to={href}>
      <Stack
      py={2}
      px={6}
      m={0}
      direction={'row'}
      align={'center'}>
        <Box>
          <Text
            transition={'all .3s ease'}
            color="#1a1a1a"
            borderBottom="2px solid #ffffff00"
            _hover={{ color: 'orange', borderBottom: '2px solid orange'}}
            fontWeight={location.pathname === child.path  ? '700' : '500'}>
            {label}
          </Text>
          
        </Box>
      </Stack>
    </Link>
  );
};

  export default DesktopSubNav;