import React from "react"
import {
    Stack,
    useColorModeValue,
} from '@chakra-ui/react'
import MobileNavItem from './MobileNavItem'
import { useStaticQuery, graphql } from "gatsby"
import { flatListToHierarchical } from "../../utils"


const MobileNav = ( {menuData , menuName, displayLocation} ) => {
  let data = useStaticQuery(graphql`
    {
      allWpMenuItem(
        sort: { fields: order, order: ASC }
        filter: { menu: { node: { name: { eq: "primary" } } } }
      ) {
        nodes {
          id
          title: label
          path
          target
          parent: parentId
        }
      }
    }
  `)

const displayMobile = ["block", "block", "block", "none"]
const displaySecondary = ["none", "none", "none", "block"]
var displayVal = []

  if(menuName === 'secondary'){
    data = menuData
    displayVal = displaySecondary
  }
  else{
    displayVal = displayMobile
  }


  const NAV_ITEMS = flatListToHierarchical(data.allWpMenuItem.nodes, {
    idKey: "id",
    childrenKey: "routes",
    parentKey: "parent",
  })

  

  return (
    <Stack
      bg={useColorModeValue('white', 'gray.800')}
      p={4}
      mt={menuName === 'secondary' ? '4' : 20}
      display={displayVal}>
      {NAV_ITEMS.map((navItem) => (
        <MobileNavItem key={navItem.id} {...navItem} />
      ))}
    </Stack>
  );
};

//<MobileNavItem key={navItem.label} {...navItem} />

  export default MobileNav

  